* {
  box-sizing: border-box; }

body {
  background: #F5F3EF;
  color: #2e2e2e; }

button {
  background: #3BA9E7;
  border-radius: 20px;
  border: none;
  color: #ffffff;
  text-align: center;
  padding: 10px 30px;
  font-size: 1em;
  cursor: pointer; }

.form__button {
  background: #64B300; }

.button__clear-storage {
  background: #F49B5B; }

.form {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center; }

.form__input {
  width: 400px;
  border-radius: 20px;
  height: 39px;
  border: 1px solid #cccccc;
  padding-left: 10px;
  margin-right: 25px; }

.button__clear-storage {
  position: absolute;
  top: 0;
  right: 0; }

.cards {
  display: flex;
  flex-wrap: wrap;
  margin-top: 50px; }

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 450px;
  height: auto;
  border: 1px solid #cccccc;
  border-radius: 4px;
  text-align: center;
  padding: 10px;
  justify-content: space-between;
  background: #ffffff; }

.card:not(:first-child) {
  margin-left: 16px; }

.card:hover {
  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.25); }

.card__img {
  width: 400px;
  height: 260px;
  margin-bottom: 16px; }

.card__url {
  margin-bottom: 16px; }

.card__title, .card__descr, .card__url {
  overflow: auto; }

.card__title::after {
  display: block;
  content: '';
  width: 400px;
  height: 1px;
  background: #cccccc;
  margin: 0 auto;
  margin-top: 10px; }
